}
.left-sideBar{
  flex-grow: 1; /* default 0 */
  border: 1px solid black;
  height: 100%;
}
.center-content{
  flex-grow: 2; /* default 0 */
  border: 1px solid black;
  height: 100%;
}
.right-sideBar{
  flex-grow: 1; /* default 0 */
  border: 1px solid black;
  height: 100%;
}
